<template>
	<view class="container">
		<view>
			<tn-swiper :list="bannerList" titleName="title" name="avatar" :title="true" :height="300"></tn-swiper>
		</view>
		<view class="notice">
			<tn-notice-bar :list="noticeList" mode="horizontal" backgroundColor="tn-bg-indigo"
				fontColor="#FFFFFF"></tn-notice-bar>
		</view>
		<view>
			<tn-tabs :list="categoryList" :isScroll="true" :current="current" name="name" @change="change"></tn-tabs>
		</view>
		<view class="article-list">
			<view class="" v-if="articleList.length">
				<div class="article-item" v-if="item.id != 200" v-for="(item,index) in articleList" :key="index" @tap="gonavigate(item.id)">
					<div class="title">{{item.title}}</div>
					<div class="desc">{{item.summary}}</div>
					<div class="bottom">
						<tn-avatar class="user-avatar" size="sm" :src="item.userAvatar"></tn-avatar>
						<span class="user-nickname">{{item.username}}</span>
					</div>
				</div>
				<uni-load-more :status="status" :content-text="contentText"
					@clickLoadMore="handlePage" />
			</view>


			<view v-else>
				<tn-empty mode="list"></tn-empty>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		getArticleList
	} from '@/api/article.js'
	import {
		getIndexList
	} from '@/api/index.js'
	import {
		getCategoryList
	} from '@/api/category.js'
	export default {
		data() {
			return {
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多内容了'
				},
				href: 'https://uniapp.dcloud.io/component/README?id=uniui',
				list: [{
						image: 'https://resource.tuniaokj.com/images/swiper/spring.jpg',
						title: 'xxx'
					},
					{
						image: 'https://resource.tuniaokj.com/images/swiper/autumn.jpg',
						title: 'xxx'
					},
				],
				noticeList: [
					'拾壹博客现已发布V1.0.0'
				],
				articleList: [],
				categoryList: [{
					"name": "最新",
					id: null
				}],
				bannerList:[],
				current: 0,
				params: {
					pageNo: 1,
					pageSize: 10
				},
				pages: 0,
				status: "more"
			}
		},
		onShow() {
			this.params.categoryId = 0
			this.getArticleList();
			this.getCategoryList();
			this.handleBannerList();
		},
		methods: {
			handleBannerList(){
				getIndexList().then(res =>{
					this.bannerList = res.extra.articles
				})
			},
			// 分页
			handlePage(val) {
				if (val.detail.status == "noMore") {
					return
				}
				this.status = "loading";
				this.params.pageNo++;
				uni.showLoading({
					title: "加载中",
					mask: true
				});
				getArticleList(this.params).then(res => {
					this.articleList.push(...res.data.records)
					this.pages = res.data.pages
					this.status = this.params.pageNo == this.pages ? "noMore" : "more"
					uni.hideLoading()
				})
			},
			gonavigate(id) {
				uni.navigateTo({
					//保留当前页面，跳转到应用内的某个页面
					url: '/pages/article/index?id=' + id
				})
			},

			getArticleList() {
				uni.showLoading({
					title: "加载中",
					mask: true
				});
				getArticleList(this.params).then(res => {
					this.articleList = res.data.records
					this.pages = res.data.pages
					if (this.params.pageNo == this.pages) {
						this.status = "noMore"
					}
					uni.hideLoading()
				})
			},
			getCategoryList() {
				getCategoryList().then(res => {
					this.categoryList.push(...res.data)
				})
			},
			change(index) {
				const category = this.categoryList[index];
				this.params.pageNo = 1;
				this.params.categoryId = category.id ? category.id : 0;
				this.status = "more"
				this.getArticleList();
				this.current = index;
			}
		}
	}
</script>

<style lang="scss" scoped>
	.container {
		padding: 10px;
		font-size: 14px;

		.notice {
			margin: 15px 0;
		}

		.article-list {
			.article-item {
				margin-bottom: 10px;
				padding: 10px;
				background-color: #fff;
				border-radius: 5px;

				.title {
					font-weight: 700;
					font-size: 1rem;
				}

				.desc {
					margin: 10px 0;
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					color:#838383;
				}

				.bottom {
					.user-avatar {
						vertical-align: middle;
					}

					.user-nickname {
						margin: 0 10rpx;
						color:#838383;
					}
				}
			}
		}
	}
</style>